// src/server/index.ts
import type { Router, Request, Response, RequestHandler } from "express";
import { Router as createRouter } from "express";
import { routes } from "../shared/routes.js";

type HandlerMap = Record<string, RequestHandler>; // key = name

export function makeExpressRouter(handlers: HandlerMap): Router {
  const r = createRouter();

  for (const def of routes) {
    const handler =
      (def.name && handlers[def.name]) ||
      ((req: Request, res: Response) => res.status(501).json({ error: "Not implemented", route: def.name ?? def.path }));

    switch (def.method) {
      case "GET":    r.get(def.path, handler); break;
      case "POST":   r.post(def.path, handler); break;
      case "PUT":    r.put(def.path, handler); break;
      case "PATCH":  r.patch(def.path, handler); break;
      case "DELETE": r.delete(def.path, handler); break;
    }
  }
  return r;
}

export { routes };